@import url(https://fonts.googleapis.com/css2?family=Andika+New+Basic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&display=swap);
html, body {
  margin: 0;
  padding: 0;
 
  --stripe: #ffdb83;
  --bg: #ffdb83;

  background: linear-gradient(135deg, var(--bg) 25%, transparent 25%) -50px 0,
    linear-gradient(225deg, var(--bg) 25%, transparent 25%) -50px 0,
    linear-gradient(315deg, var(--bg) 25%, transparent 25%),
    linear-gradient(45deg, var(--bg) 25%, transparent 25%);
  background-size: 100px 100px;
  background-color: var(--stripe);
}

.App {
  padding: 20px;
}

nav.navbar { 
  display: flex;
  justify-content: flex-end;
  padding: 0px 20px;
  background: #ffdb83; 
}

button.navbar-button {
  padding: 5px 20px;
  width: 100px;
  margin: 15px 10px;
  border-radius: 3px;
}

#home-btn {
  color: white;
  text-decoration: none;
}

.container {
  padding: 0 !important;
}
.home {
  text-align: center;
  height: 100vh;
  padding: 25px;
}

.logo {
  width: 250px;
  height: auto;
  margin: 40px 0;
}

.home h1 {
  font-family: "Andika New Basic", sans-serif;
  color: black;
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: bold;
  text-shadow: 4px 3px 0px #fff, 9px 8px 0px rgba(0, 0, 0, 0.15);
}

.signup-button {
  margin-top: 50px;
  background-color: #000 !important;
  border: none !important ;
}

.login-button {
  margin-top: 50px;
  background-color: #2eafa5 !important;
  border: none !important ;
}

.buttons {
  padding: 5%;
}

.aboutme {
  padding: 25px;
  height: 100vh;
}

.aboutme h1 {
  font-size: 2rem;
  margin-bottom: 20px;
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000,
    -1px 1px 0 #000, 1px 1px 0 #000;
  color: white;
}

.aboutme h2 {
  font-size: 1.3rem;
  margin-bottom: 30px;
}

.table {
  background-color: #2eafa5;
  text-align: center;
  font-family: "Oswald", sans-serif;
  border-radius: 25px;
  box-shadow: 5px 5px rgba(0, 98, 90, 0.4), 10px 10px rgba(0, 98, 90, 0.3),
    15px 15px rgba(0, 98, 90, 0.2), 20px 20px rgba(0, 98, 90, 0.1),
    25px 25px rgba(0, 98, 90, 0.05);
  padding: 25px;
}

.table label {
  font-weight: bold;
  font-size: 1.3rem;
}

.table button {
  margin: 15px 0;
  background-color: #e75481;
  border: none;
}

.mybuddies{
      height: 100vh;
      padding: 25px;
    }
    
    .mybuddies h1 {
      text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000,
        -1px 1px 0 #000, 1px 1px 0 #000;
      color: white;
      margin-bottom: 40px;
    }
    
    .mybuddiesbutton button {
      margin-top: 10px;
      background-color: #e75481;
      border: none;
    }
    
    .avatar{
      height:100px;
    }

    .username {
      list-style-type: none;
    }
.buddydetail {
  height: 100vh;
  padding: 25px;
}

.buddydetail h1 {
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000,
    -1px 1px 0 #000, 1px 1px 0 #000;
  color: white;
  margin-bottom: 40px;
}

.buddydetail button {
  margin-top: 10px;
  background-color: #e75481;
  border: none;
}


